// src/components/reviews/ReviewListItem.tsx
"use client";

import * as React from "react";
import { cn } from "@/lib/utils";
import type { Review } from "@/lib/types";
import { Badge } from "@/components/ui/primitives/Badge";

const shellBase = cn(
  "relative w-full text-left rounded-card r-card-lg p-[var(--space-3)] bg-card/90 border border-border/35 transition-all duration-motion-sm focus-visible:outline-none disabled:opacity-disabled disabled:pointer-events-none disabled:bg-muted/20",
  "hover:bg-[theme('colors.interaction.accent.tintHover')] hover:ring-2 hover:ring-[var(--theme-ring,var(--focus))]",
  "focus-visible:bg-[theme('colors.interaction.accent.tintHover')] focus-visible:ring-2 focus-visible:ring-[var(--theme-ring,var(--focus))]",
  "active:bg-[theme('colors.interaction.accent.tintActive')] active:ring-2 active:ring-[var(--theme-ring,var(--focus))]",
  "data-[selected=true]:bg-[theme('colors.interaction.accent.tintActive')] data-[selected=true]:ring-2 data-[selected=true]:ring-[theme('colors.interaction.accent.surfaceActive')]",
);

const statusDotBase =
  "h-[var(--space-2)] w-[var(--space-2)] rounded-full ring-2";
const statusDotWin = "bg-success ring-success";
const statusDotLoss = "bg-danger ring-danger";
const statusDotDefault = "bg-muted-foreground ring-muted-foreground";
const statusDotPulse =
  "motion-safe:animate-[pulse_2s_ease-in-out_infinite] motion-reduce:animate-none";
const statusDotBlink =
  "motion-safe:animate-[blink_1s_steps(2)_infinite] motion-reduce:animate-none";
const itemLoading = cn(
  shellBase,
  "motion-safe:animate-pulse motion-reduce:animate-none",
);
const loadingLine = "h-[var(--space-3)] rounded-card bg-muted";
const scoreBadge = cn(
  "px-[var(--space-2)] py-[var(--space-1)] rounded-full text-ui leading-none font-medium",
  "text-accent-2-foreground ring-1 ring-[theme('colors.interaction.info.surfaceActive')] bg-accent-2",
  "hover:ring-[theme('colors.interaction.info.surfaceHover')]",
  "focus-visible:ring-[theme('colors.interaction.info.surfaceHover')]",
  "active:ring-[theme('colors.interaction.info.surfaceActive')]",
);

export interface ReviewListItemProps
  extends Omit<React.ButtonHTMLAttributes<HTMLButtonElement>, "children"> {
  review?: Review;
  selected?: boolean;
  loading?: boolean;
}

const ReviewListItemInner = (
  {
    review,
    selected = false,
    loading = false,
    className,
    onClick,
    disabled,
    ...rest
  }: ReviewListItemProps,
  ref: React.ForwardedRef<HTMLButtonElement>,
) => {
  if (loading) {
    return (
      <div data-scope="reviews" className={itemLoading}>
        <div
          className={cn(loadingLine, "w-3/5 mb-[var(--space-3)]")}
        />
        <div className={cn(loadingLine, "w-2/5")} />
      </div>
    );
  }

  const title = review?.title?.trim() || "Untitled Review";
  const untitled = !review?.title?.trim();
  const matchup = review?.matchup?.trim() || "";
  const score = review?.score;
  const role = review?.role;
  const isDisabled = disabled ?? false;

  const { ["aria-label"]: ariaLabelProp, type = "button", ...buttonProps } = rest;
  const ariaLabel =
    ariaLabelProp ?? (title ? `Open review: ${title}` : undefined);

  return (
    <button
      ref={ref}
      data-scope="reviews"
      type={type}
      disabled={isDisabled}
      onClick={onClick}
      aria-label={ariaLabel}
      aria-current={selected ? "true" : undefined}
      data-selected={selected ? "true" : undefined}
      className={cn(shellBase, className)}
      {...buttonProps}
    >
      <div className="flex flex-col gap-[var(--space-2)]">
        <div className="flex items-start justify-between gap-[var(--space-2)]">
          <div
            className={cn(
              "truncate font-medium text-body",
              untitled && "text-muted-foreground/70",
            )}
            aria-label={untitled ? "Untitled Review" : undefined}
          >
            {title}
          </div>
          {matchup ? (
            <div className="truncate text-ui text-muted-foreground">
              {matchup}
            </div>
          ) : null}
        </div>
        <div className="flex items-center justify-between gap-[var(--space-2)]">
          <div className="flex items-center gap-[var(--space-2)]">
            <span
              aria-hidden
              className={cn(
                statusDotBase,
                statusDotBlink,
                review?.result === "Win"
                  ? statusDotWin
                  : review?.result === "Loss"
                    ? statusDotLoss
                    : statusDotDefault,
                review?.status === "new" && statusDotPulse,
              )}
            />
            {role ? (
              <Badge
                size="sm"
                tone="neutral"
                className="px-[var(--space-1)]"
              >
                {role}
              </Badge>
            ) : null}
          </div>
          {typeof score === "number" ? (
            <span
              className={scoreBadge}
              aria-label={`Rating ${score} out of 10`}
            >
              {score}/10
            </span>
          ) : null}
        </div>
      </div>
    </button>
  );
};

export const ReviewListItem = React.forwardRef<HTMLButtonElement, ReviewListItemProps>(
  ReviewListItemInner,
);

ReviewListItem.displayName = "ReviewListItem";
